.additional-goods {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-bottom: 35px;
  padding-top: 25px;
}

.container {
  max-width: 1300px;

  margin: 0 auto;
}

@media (min-width: $mobile-width-only) {
  .container {
    padding-left: 20px;
  }
}

.extra__wrapper {
  position: relative;

  &::before {
    content: "";
    background-color: $button-color;
    position: absolute;
    top: 15px;
    right: 0;
    z-index: -1;
    width: 100%;
    height: 2px;
  }
}

.additional-goods__title {
  padding-left: 20px;
  padding-right: 30px;
  font-size: 24px;
  line-height: 1;
  background-color: $button-text-color;
  color: $color-title;
  font-family: $font;
  font-weight: 400;
  display: inline-flex;
  padding-bottom: 36px;
  margin: 0;
}

.additional-goods-list__title {
  text-align: left;
  margin: 0 0 0 0;
  min-width: 280px;
  padding-bottom: 10px;
}

.additional-goods__item {
  display: flex;
  position: relative;
  flex-direction: column;
  min-height: 98px;
  padding: 13px 0 19px 0;

  .site-list__list {
    display: flex;
    justify-content: space-between;
  }

  &::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    border-top: 1px solid $special_dark-grey-3;
  }

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    border-top: 1px solid $special_dark-grey-3;
  }
}

.additional-goods__item .button {
  margin: auto 0 0 0;
  width: 100%;
  max-height: 20px;
}

.gif {
  max-height: 289px;
  box-sizing: border-box;
  margin-top: 40px;
  background-repeat: no-repeat;
  background-image: url("../../img/gift-photo-mobile.jpg");
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../../img/gift-photo-mobile@2x.jpg");
  }
}

.gif__wrapper {
  display: flex;
  flex-direction: column;
  color: white;
  align-items: center;
  padding: 58px 60px 33px 60px;
}

.gif__text {
  align-items: center;
  font-size: 16px;
  line-height: 1.2;
  font-style: "Arial";
  text-align: center;
}

@media (min-width: $mobile-width-only) {
  .additional-goods__title {
    padding-top: 20px;
  }
}

@media (min-width: $tablet-width) {
  .gif {
    max-height: 200px;
    margin-top: 70px;
    background-image: url("../../img/gift-photo-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/gift-photo-tablet@2x.jpg");
    }
  }

  .container {
    padding-left: 40px;
    padding-right: 40px;
  }

  .gif__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 58px 124px 33px 80px;
  }

  .extra__wrapper {
    &::before {
      top: 40px;
    }
  }

  .gif__text {
    max-width: 162px;
    text-align: left;
  }

  .additional-list {
    margin: 80px 0 0 0;
  }

  .additional-goods {
    margin-top: 24px;
    padding-bottom: 80px;
  }

  .additional-goods__item {
    padding: 26px 38px 28px 38px;
    min-height: 84px;
    min-width: 634px;
    outline: 1px solid $special_dark-grey-3;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    &::after {
      border-top: none;
    }

    .site-list__list {
      margin-left: auto;
    }

    .site-list__description {
      margin-left: 51px;
    }
  }

  .additional-goods__title {
    font-size: 36px;
    padding-bottom: 0;
  }
}

@media (min-width: $desktop-width) {
  .additional-goods {
    flex-direction: column;
    margin-top: 35px;
    margin-bottom: 10px;
  }

  .additional-goods__wrapper {
    margin-top: 81px;
    display: flex;
    position: relative;
  }

  .additional-list {
    margin-top: 0;
  }

  .additional-goods__item {
    flex-direction: row;
    min-width: 895px;
    min-height: 43px;
    justify-content: space-between;
    align-items: center;
    padding: 14px 0 15px 0;
    outline: none;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      width: 100%;
      border-top: 1px solid $special_dark-grey-3;
    }

    &::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: 72px;
      width: 100%;
      border-top: 1px solid $special_dark-grey-3;
    }
  }

  .additional-goods__item .button {
    width: 244px;
    max-height: 20px;
    margin-top: 0;
  }

  .additional-goods__item .site-list__list {
    width: 286px;
    margin-right: 40px;
  }

  .gif {
    max-height: 289px;
    width: 245px;
    margin-top: 0;
    margin-left: 80px;
    background-image: url("../../img/gift-photo-mobile.jpg");
    background-size: cover;

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/gift-photo-mobile@2x.jpg");
    }
  }

  .additional-goods-list__title {
    margin: 0 auto;
  }

  .site-list__product-item .site-list__information {
    min-width: 168px;
  }

  .gif__wrapper {
    display: flex;
    flex-direction: column;
    color: white;
    align-items: center;
    padding: 58px 60px 33px 60px;
  }

  .gif__text {
    align-items: center;
    font-size: 16px;
    line-height: 1.2;
    font-style: "Arial";
    text-align: center;
  }

  .additional-goods__title__wrapper {
    height: 50px;
  }

  .additional-goods__title {
    height: 50px;
  }
}
